import { Cliente } from './../../models/cliente';
import { FormBuilder } from '@angular/forms';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, ViewController } from 'ionic-angular';

import { User } from '../../providers';
import { SearchService } from '../../providers/api/search.service';

@IonicPage()
@Component({
  selector: 'page-select-cliente',
  templateUrl: 'select-cliente.html'
})
export class SelectClientePage {
  clientes: any[];
  queryText = '';

  constructor(public navCtrl: NavController, public navParams: NavParams,
    public formBuilder: FormBuilder,
    public modalCtrl: ModalController,
    public params: NavParams,
    public viewCtrl: ViewController,
    public searchService: SearchService,
    public user: User
  ) { }


  ionViewDidLoad() {
    this.clientes = this.params.get('clientes');
  }

  openItem(cliente: Cliente) {
    this.viewCtrl.dismiss(cliente);
  }

  closeModal() {
    this.viewCtrl.dismiss(null);
  }

  onIonInput(event) {
    //const val = event.target.value;
    /*this.searchService.getClientes({cliente: val, clienteMae: this.user._user.cliMae})
    .subscribe((res:any) => {
      this.clientes = res.data;
    },error => {
      // loading.dismiss();
    },() => {
      // loading.dismiss();
    });*/
  }
 

}



